*{margin:0;padding:0;box-sizing:border-box; list-style-type: none;}
html{font-size:10px;}
body{font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; height:100vh; display:flex; justify-content: center; align-items: center; background-color: midnightblue;}

.faqContainer{width:70rem; display:flex; flex-direction: column; gap:2rem; text-align:center; background-color: aquamarine; border-radius:1rem; padding:4rem;}
h1{font-size:3rem;}

.accordion{display: flex; flex-direction:column;}

.question{background-color: aliceblue; padding:1rem 2rem; display:flex; justify-content: space-between; align-items: center;}

button{border:none; background:none; }

.answerbox{padding:2rem; background-color: bisque; text-align: left; font-size:1.6rem;  border-top:0.1rem solid rgb(48, 100, 83); height:auto; transition:all 0.3s ease;}

.ht-0{height:0; padding:0; overflow:hidden;border:none;}

